// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";
@import "./components/footer";



.logo{
    font-size: 2rem;
    font-weight: 600;

    &--footer{
        color:#fff;
        
    }
}
.main {
    display: flex;
    justify-content: space-between;
}

 .main{
    background-image: url(../img/main-cover.jpg);
     background-repeat: no-repeat;
    background-position: top right;
    height: 84rem;
    position: relative;
 }

.main__left{
    max-width: 47.4rem;
    margin-top: 6.3rem;
}
.main__title{
    
    font-weight: bold;
    font-size: 6rem;
    line-height: 70px;
    letter-spacing: -1px;
    
}
.main__description{
  
    margin:3rem 0 5.9rem 0;
    font-size: 1.8rem;
    line-height: 28px;
    opacity: .7;
}

.main-right__video{
    position: absolute;
    left: 58rem;
    top: 36rem;
    width: 40rem;
    height: 36rem;
    background-image: url(../img/video-bg.jpg);
    background-repeat: no-repeat;
}
.main-right__video-wrapper{
    position: relative;
}
.main-right__video-play{
    
    background-color: #fff;
    left: 50%;
    transform: translateX(-50%);
    padding: 21px 22px 21px 25px;
    top: 130px;
    position: absolute;
    cursor: pointer;
    
    &--services {
    
        top: 50%;
        transform: translate(-50%, -50%);
    }
}

.about,
.features
{
    margin-top: 13.3rem;
    display: flex;
    justify-content: flex-end;
    background-image: url(../img/quote-bg.jpg);
    background-repeat: no-repeat;

    &--background{
        background-image: url(../img/features-bg.jpg);
    }
}
.about__right,
.features__right
{
    max-width: 54rem;
    
    
    &--mb {
        margin-bottom: 28.3rem;
    }
}


.about__right-author{
    display: flex;
    margin-bottom: 16.7rem;
}
.about-author__img{
    img{
        border-radius: 50%;
    }
}
.about-author__text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 3rem;
}
.about-author__post{
    opacity: .7;
    font-size: 18px;
    line-height: 28px;
}

.services__wrapper{
    display: flex;
    justify-content: space-between;
    margin: 4rem 0 9.9rem 0;
}
.services__tags{
    display: flex;
}
.services__list{
    margin-left: 22rem;
}
.services{
    margin-top: 16.7rem;
    margin-bottom: 30rem;
    
}

.services__slider-item {
    opacity: .5;
    margin-right: 3rem;
}
.slick-current{
    opacity: 1;
}
.services__slider-img{
    position: relative;
    max-width: 82.5rem;
    
}
.features__right-author{
    display: flex;
    align-items: center;
    margin-top: 3rem;
}

.features__right{
    position: relative;
}

.features__img{
    position: absolute;
    left: -42rem;
    top: 10rem;
}

.works{
    margin-bottom: 25rem;
}
.works__head{
    margin:4rem 0 10.8rem 0;
    display: flex;
    justify-content: space-between;
}
.works__tabs-list{
    display: flex;
    
    li{
        margin-left: 2.5rem;
        cursor: pointer;
        letter-spacing: 4px;
        opacity: 0.3;

        &:hover{
            color:#1434E1;
            opacity: 1;
        }
    }
}
.works__gallery{
    display: flex;
    flex-wrap: wrap;
    
}
.works__gallery-item{

    .main-right__video-play{
        transition: all .2s ease-in-out;
        opacity: 0;
    }
    position: relative;
    
    margin-right: 3rem;
    margin-left: 6rem;
    margin-bottom: 3rem;
    cursor: pointer;
    transition: all .2s ease-in-out;
    
    &:hover{
        transform: translateY(-2rem);

        .main-right__video-play{
            opacity: 1;
        }
    }
}
.works__footer{
    display: flex;
    max-width: 140rem;
    flex-wrap: wrap;
    justify-content: space-between;
    padding:0 7rem;
}
.works__footer-text {
    max-width: 73rem;
    font-size: 18px;
    line-height: 28px;
    opacity: 0.7;
}
.testimonials__slider-item{
    background-image: url(../img/testimonials2.jpg);
    background-position: top right;
    background-repeat: no-repeat;
    padding-bottom: 20rem;
}
.testimonials__slider-item-wrapper{
    max-width: 55rem;
}
.clients__head{
    justify-content: space-between;
    display: flex;
}

.clients__text{
    padding: 8rem 0 12rem 0;
}
.section__text-description{
    max-width: 45rem;
    font-size: 18px;
    line-height: 28px;
    opacity: 0.7;
}
.clients{
    padding-top: 5rem;
    background-color: #fff;
}
.clients__firms{
    display: flex;
    justify-content: space-between;
    padding-bottom: 14rem;
    padding-top: 7rem;
}